.product {
  @include centeredGrid;
  grid-template-columns: repeat(4, 200px);
  grid-column-gap: 48px;
  grid-row-gap: 50px;
  margin: 50px auto;

  &-style {
    font-size: 11px;
    font-weight: 400;
  }
  
  &__card {
    display: block;
    box-sizing: border-box;
    align-items: center;
  
    width: 200px;
    padding: 15px 14px;
    
    background: #fff;
    border: 1px solid #f3f3f3;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
    border-radius: 5px;

  }
  
  &__photo {
    display: block;
    box-sizing: content-box;
    width: 160px;
    height: 134px;
    margin: 15px auto 44px;

    &:hover {
      border: 3px solid #f3f3f3;
      margin-top: 0;
      margin-bottom: 29px;
      padding: 12px 0;
      animation: imghover 1s;
      animation-fill-mode: forwards;
      
      @keyframes imghover {
        from {
          transform: scale(1);
        }
        to {
          transform: scale(1.2);
        }
      }
    }
  }
  
  &__name {
    text-align: left;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
    margin-bottom: 6px;
  }
  
  &__code {
    margin-bottom: 15px;

    &::before {
      content: "Код товара: ";
    }
  }
    
  /* rating & review block */
  
  &__rating {
    @include spaceBetweenGrid;
    grid-template-columns: 100px auto;
    align-items: center;
    margin-bottom: 20px;
  }
  
  &__reviews {
    margin: 0;

    &::before {
      content: "Отзывов: ";
    }
  }
  
  /* price block */
  
  &__price {
    @include spaceBetweenGrid;
    grid-template-columns: 50px auto;
    align-items: baseline;
    margin-bottom: 20px;
  }
  
  &__price-amount {
    font-size: 16px;
    font-weight: 700;
  }
  
  &__buy {
    @include centeredGrid;
    align-items: center;
    height: 38px;
  
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
  
    color: white;
    background-color: $mainBlueColor;
    border: 1px solid $mainBlueColor;
    border-radius: 5px;
    transition: color 0.3s, background-color 0.3s;

    &:hover {
      background-color: #fff;
      color: $mainBlueColor;
    }

  }

}
